/* Site Colors */
:root {
  --primary-color: #000980;
  --white: #ffffff;
  --accent-color: #ED4C37;
}

/* Global Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  color: var(--primary-color);
  font-family: 'Playfair Display', serif;
  max-width: 1800px;
  margin: 0 auto;
}

.page-wrapper {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  margin: 0 3%;
}

.button,
button {
  display: inline-block; 
  padding: .6em .9em;
  border: solid 3px var(--primary-color);
  background-color: var(--white);
  border-radius: 4px;
  font-family: 'Roboto', sans-serif;
  font-weight: bold;
  letter-spacing: .05em;
  text-decoration: none;
  color: var(--primary-color);
  cursor: pointer;
  outline: none;
}

.button:hover,
button:hover,
button:focus {
  background-color: var(--accent-color);
  border-color: var(--accent-color);
  color: var(--white);
}

form label,
form input,
form textarea {
  display: block;
  width: 100%;
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  color: var(--primary-color);
}

form label {
  margin: 0 0 3px 0;
  font-weight: 500;
}

form input,
form textarea {
  padding: 6px;
  resize: none;
  border: solid 3px var(--primary-color);
  border-radius: 4px;
}

form input:focus,
form textarea:focus {
  outline-width: 3px;
  outline-color: var(--accent-color);
  border-width: 3px;
  border-color: var(--accent-color);
}

form button {
  margin-top: 3px;
  background-color: var(--primary-color);
  color: var(--white);
  font-size: 16px;
}

form input.error,
form textarea.error {
  background-color: rgba(237,76,55,.2);
}

form .success-message {
  font-family: 'Roboto', sans-serif;
  line-height: 1.1em;
  font-style: italic;
  font-weight: 500;
  padding: 0px;
  margin-top: 20px;
}

form .error-message {
  font-family: 'Roboto', sans-serif;
  font-size: 14px;
  line-height: 1.1em;
  font-style: italic;
  font-weight: 500;
  color: var(--accent-color);
  padding: 0px;
  margin: 3px 0 6px 0;
}

.fa-spinner {
  margin-left: 10px;
  -webkit-animation: spin 2s linear infinite;
  animation: spin 1.5s linear infinite;
}

@-webkit-keyframes spin {
  0% { -webkit-transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); }
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.list {
  font-family: 'Roboto', sans-serif;
  margin: 10px 0;
}

.list h3{
  font-size: 22px;
  margin-bottom: 6px;
}

.list ul {
  list-style-position: inside;
  margin-bottom: 10px;
  font-size: 18px;
}

.list ul li {
  margin: 5px;
}

/* Header Styles */
header {
  font-family: 'Roboto', sans-serif;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

header h1,
header nav {
  width: 100%;
}

header h1 {
  margin: 30px 0 20px 0;
  text-transform: uppercase;
  font-size: 34px;
  line-height: 1.3em;
  letter-spacing: .2em;
  text-align: center;
}

header h1 a {
  color: var(--primary-color);
  text-decoration: none;
}

header nav ul {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  justify-content: space-around;
}

header h1 span {
  cursor: pointer;
}

header nav span {
  display: inline-block;
  padding: 14px 6px 10px 6px;
  font-size: 16px;
  font-weight: 500;
  letter-spacing: .1em;
  cursor: pointer;
  border-bottom: solid 4px rgba(0,0,0,0);
}

header nav span.current {
  border-bottom: solid 4px var(--primary-color);
}

header nav span:hover {
  color: var(--accent-color);
}

header nav span.current:hover {
  border-bottom: solid 4px var(--accent-color);
}

/* Main Content Styles */
main {
  flex: 1 0 auto;
}

main section {
  margin-top: 30px;
  margin-bottom: 20px;
}

main section .title-wrapper {
  text-align: center;
  margin-bottom: 30px;
}

main section .title-wrapper h2 {
  box-sizing: content-box;
  color: var(--white);
  background-color: var(--primary-color);
  width: 60px;
  height: 60px;
  border-radius: 50%;
  font-weight: 400;
  font-size: 42px;
  display: inline-block;
}

main section p {
  clear: both;
  font-size: 18px;
  line-height: 1.75em;
  padding: 12px 0;
}

main section .paragraph-start {
  font-size: 1.1em;
  font-weight: 700;
}

/* About Content Styles */
#intro {
  margin: 40px 8% 80px 8%;
  text-align: center;
}

#intro p {
  font-size: 32px;
  line-height: 1.5em;
  max-width: 700px;
}

#intro img {
  margin-bottom: 20px;
  width: 250px;
  height: auto;
}

#about {
  display: flex;
  flex-wrap: wrap;
}

#about .title-wrapper,
#about .content-wrapper {
  width: 100%;
}

#about .title-wrapper h2 {
  padding: 45px 110px 65px 0;
  margin-right: 140px;
}

#about img {
  width: 65%;
  height: auto;
  margin-top: -50px;
  margin-left: 60px;
}

/* Portfolio Content Styles */
#portfolio .title-wrapper {
  text-align: left;
}

#portfolio .title-wrapper h2 {
  font-size: 34px;
  margin-left: 15%;
  padding: 70px 73px 20px 17px;
}

#portfolio ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

#portfolio ul li {
  list-style: none;
  width: 100%;
  margin: 40px 0;
}

#portfolio ul li img {
  width: 100%;
  height: auto;
}

#portfolio h3 {
  padding-top: 20px;
  font-weight: bold;
  font-size: 24px;
  letter-spacing: .04em;
}

#portfolio p {
  font-family: 'Roboto', sans-serif;
  font-weight: normal;
  font-size: 18px;
  letter-spacing: .04em;
  line-height: 1.4em;
  padding: .5em 0;
}

#portfolio .button {
  margin-right: 10px;
}

/* Contact Content Styles */
#contact {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

#contact .content-wrapper {
  width: 100%;
  max-width: 500px;
}

#contact .title-wrapper h2 {
  font-size: 38px;
  padding: 80px 98px 20px 2px;
  margin: 0 50px;
}

/* Resume Content Styles */
#resume .title-wrapper h2 {
  padding: 45px 88px 75px 33px;
}

#resume .content-wrapper {
  max-width: 900px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

#resume .list {
  width: 90%;
  margin: 10px 5%;
}

#resume .download {
  width: 100%;
  text-align: center;
  margin-top: 20px;
}

/* Footer Styles */
footer {
  flex-shrink: 0;
  margin: 30px 0;
}

footer ul {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  justify-content: center;
}

footer ul li {
  padding: 0 26px;
  margin-bottom: 20px;
}

footer ul li a {
  font-size: 30px;
  color: var(--primary-color);
}

footer ul li a:hover {
  color: var(--accent-color);
}

footer .legal {
  font-family: 'Roboto', sans-serif;
  font-size: 14px;
  opacity: .6;
  text-align: center;
  margin-top: 20px;
}


/* Responsive Styles */
@media screen and (min-width: 576px) {
  .page-wrapper {
    margin: 0 5%;
  }

  #about img {
    width: 50%;
  }

  #resume .list {
    width: 40%;
  }
}
@media screen and (min-width: 768px) {
  header {
    margin-top: 10px;
  }
  
  header h1 {
    font-size: 36px;
  }

  header nav span {
    font-size: 18px;
  }

  #intro {
    display: flex;
    flex-wrap: wrap;
    margin: 70px 8% 120px 8%;
  }

  #intro .img-wrapper {
    width: 40%;
    height: auto;
    text-align: right;
  }

  #intro .img-wrapper img {
    width: 100%;
    max-width: 350px;
    height: auto;
  }

  #intro p {
    font-size: 36px;
    width: 65%;
    margin-left: -5%;
    margin-top: 100px;
    text-align: left;
  }

  #about .title-wrapper {
    width: 30%;
  }
  
  #about .content-wrapper {
    width: 70%;
    padding-left: 10px;
  }

  #about img {
    width: 90%;
    margin-top: -30px;
    margin-left: 0;
  }

  #portfolio .title-wrapper h2 {
    font-size: 42px;
    margin-left: 10%;
    padding: 90px 105px 40px 25px;
  }

  #portfolio ul li:not(:first-child) {
    width: 40%;
    margin: 60px 0;
  }

  footer ul li a {
    font-size: 40px;
  }

  footer {
    margin-top: 60px;
  }
}

@media screen and (min-width: 992px) {
  #intro .img-wrapper img {
    width: 80%;
  }

  #intro p {
    font-size: 44px;
  }

  #portfolio ul li:first-child {
    margin-top: -80px;
    text-align: right;
  }

  #portfolio ul li:first-child img {
    width: 70%;
  }
}

@media screen and (min-width: 1200px) {
  header h1,
  header nav {
    width: auto;
  }

  header nav span {
    padding: 20px 10px 10px 10px;
    margin: 0 25px;
    font-size: 20px;
  }

  footer {
    margin-top: 90px;
  }
}

@media screen and (min-width: 1400px) {
  main section p {
    font-size: 22px;
  }

  #intro {
    margin: 100px 8% 180px 8%;
  }

  #intro p {
    font-size: 48px;
    margin-top: 150px;
    width: 60%;
  }

  #about img {
    width: 60%;
    margin-top: -50px;
    margin-left: 70px;
  }

  #portfolio ul li:first-child img {
    width: 80%;
  }
}